@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&family=Quicksand:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  overflow: hidden;
}

/* Header */

.header {
  background: #122333;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

/* Header-Logo */

.header .logo img {
  height: 7vh;
  border: 3px solid aliceblue;
  border-radius: 50%;
  transition: .3s ease;
}

/* Header-Logo closed */
/* Header-Nav */

.header .nav {
  display: flex;
  width: 50vw;
  justify-content: space-evenly;
}

.header .nav a {
  text-decoration: none;
  transition: .5s ease;
}

.header .nav p {
  color: #eef6fd;
  cursor: pointer;
  font-family: 'Play', sans-serif;
  font-size: 2.7vh;
  position: relative;
}

.header .nav.hide a {
  transform: scale(0);
  pointer-events: none;
}

.header .nav p:hover span {
  color: #2eac68;
  text-shadow: 0px 0px 3px #5ce09a,
    0px 0px 2px #86e9b4;
}


.header .nav a:hover p::before {
  content: '';
  height: 3px;
  width: 50%;
  background: #2eac68;
  position: absolute;
  bottom: -0.6vh;
  left: 32%;
  border-radius: 50px;
}

.header .nav a p::before {
  content: '';
  height: 3px;
  width: 0%;
  background: #2eac68;
  position: absolute;
  bottom: -0.6vh;
  left: 0%;
  border-radius: 50px;
  transition: .5s ease;
}

.header .nav p span {
  transition: .3s ease;
  font-size: 3vh;
}

/* Header-Nav closed */
/* Header-Ham */

.header ul {
  position: relative;
  width: 2vw;
  height: 8vh;
  overflow: hidden;
  cursor: pointer;
  font-family: 'Quicksand', sans-serif;
}

.header ul li {
  list-style: none;
  position: absolute;
  transform: translateY(-50%);
  width: 70%;
  height: 6px;
  background: #eef6fd;
  transition: 1s;
  border-radius: 3px;
}

.header ul li:nth-of-type(1) {
  top: 30%;
  left: 15%;
  transition-delay: 1s;
  opacity: 1;
}

.header ul li:nth-of-type(2) {
  top: 50%;
  right: 15%;
  transition-delay: 0s;
  width: 50%;
  background: #2eac68;
}

.header ul li:nth-of-type(3) {
  top: 70%;
  right: 15%;
  transition-delay: 1s;
  opacity: 1;
}

.header ul.active li:nth-of-type(1) {
  left: 100%;
  transition-delay: 0s;
  opacity: 0;
}

.header ul.active li:nth-of-type(2) {
  transform: translateY(-50%) rotate(405deg);
  transition-delay: .5s;
  width: 70%;
}

.header ul.active li:nth-of-type(3) {
  right: 100%;
  transition-delay: 0s;
  opacity: 0;
}

.header ul li:nth-of-type(2)::before {
  content: '';
  position: absolute;
  top: -150px;
  left: 0;
  width: 100%;
  height: 100%;
  background: #2eac68;
  transform: rotate(90deg);
  transition: 1s;
  transition-delay: .5s;
  border-radius: 3px;
}

.header ul.active li:nth-of-type(2)::before {
  top: 0;
  transition-delay: 1s;
}

.home {
  height: 90vh;
  background: #122333;
}

/* Header-Ham closed */
#mouse-circle {
  position: absolute;
  width: 30px;
  height: 30px;
  margin: -20px 0px 0px -20px;
  border: 2px solid #fff;
  border-radius: 50%;
  pointer-events: none !important;
  box-shadow: 0 0 16px rgba(255, 255, 255, 0);
  opacity: 1;
  transition: .1s linear;
}

.heart-beat {
  animation: beat 1s infinite;
}

@keyframes beat {

  0%,
  100% {
    width: 30px;
    height: 30px;
    margin: -20px 0px 0px -20px;
  }

  50% {
    width: 60px;
    height: 60px;
    margin: -30px 0px 0px -30px;
  }
}

/* loading */
.load {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #18181f;
}

/* for mobiles */
@media only screen and (max-width: 768px) {

  .header .nav {
    border: solid;
    position: absolute;
    height: 100vh;
    width: 100vw;
    top: 0;
    display: flex;
    flex-direction: column;
    right: 0;
    background: transparent;
    justify-content: center;
    align-items: center;
    z-index: 8;
    -webkit-backdrop-filter: blur(10px) grayscale(1);
            backdrop-filter: blur(10px) grayscale(1);
    transition: .3s ease;
    text-shadow: 1px 1px 5px black;
  }

  .header ul {
    z-index: 9;
  }

  .header .nav.hide {
    height: 0;
  }
}
.home {
    width: 100vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.home .center {
    height: 90%;
    width: 31.5vw;
}

.home .left {
    position: relative;
    pointer-events: none;
    left: 0;
}

.home .left p {
    color: aliceblue;
    font-size: 2.2em;
    word-break: break-word;
    width: 36vw;
    height: 29vh;
    font-family: 'Sacramento', cursive;
    position: relative;
    left: 6vh;
    text-align: center;
}

.home .left p span {
    font-size: 1em;
    color: #2eac68;
    font-weight: bold;
}

.home .left p .intro-name {
    font-size: 2em;
}

.home .right {
    position: relative;
    height: 52vh;
    display: flex;
    flex-direction: column;
    right: 4vh;
    justify-content: space-evenly;
}

.home .right svg {
    font-size: 4vh;
    color: #2eac68;
    transition: .3s ease;
}

.home .right svg:hover {
    filter: drop-shadow(0px 0px 2px #2eac68);
    transform: scale(1.5);
}

@media only screen and (max-width: 768px) {
    .home {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .home .left {
        width: 100vw;
    }

    .home .left p {
        font-size: 26px;
        width: 70%;
    }

    .home .center {
        display: none;
    }

    .home .right {
        display: flex;
        width: 100%;
        justify-content: space-around;
        align-items: center;
        flex-direction: row;
        position: relative;
        left: 1vh;
    }
}
.about {
    height: 90vh;
    background: #122333;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about .container {
    width: 87%;
    height: 80%;
    background: #182b3c;
    box-shadow: 0 0 20px 6px #111d2857;
    border-radius: 19px;
    display: flex;
}

.about .container .left {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about .container .left img {
    height: 92%;
}

.about .container .right {
    width: 60%;
}

.about .container .right .morph {
    display: flex;
    height: 10vh;
}

.about .container .right p {
    color: aliceblue;
    font-family: 'Play', serif;
    padding: 0 3vh 0 0;
    font-size: 3vh;
}

.about .container .right .morph .no-padding {
    padding: 0 1vh 0 0;
}

.about .container .right h1 {
    color: #2eac68;
    font-family: 'Sacramento', cursive;
    font-size: 3em;
}

/* Text Morph */
#morph-container {
    position: relative;
    filter: url(#threshold) blur(0.6px);
}

:is(#text1, #text2) {
    position: absolute;
    font-size: 5vh;
    font-weight: 900;
    padding: 1vh 0 0 0;
    color: #2eac68;
}

/* Text Morph close*/
/* Details */
.about .container .right .details {
    display: grid;
    grid-template-columns: repeat(2, auto);
}

.about .container .right .details p {
    display: flex;
    align-items: center;
}

.about .container .right .details p svg {
    color: #2eac68;
}

.about .container .right .exp {
    display: none;
}

/* Details close*/
@media only screen and (max-width: 768px) {
    .about .container {
        display: flex;
        flex-direction: column;
        width: 80vw;
        overflow-y: scroll;
    }

    .about .container .left {
        width: 80vw;
    }

    .about .container .left img {
        width: 80%;
    }

    .about .container .right {
        width: 100%;
        text-align: center;
    }

    .about .container .right p {
        padding: 0 1vh;
    }

    .about .container .right p span {
        color: #2eac68;
    }

    .about .container .right .morph {
        display: none;
    }

    .about .container .right .exp {
        display: block;
    }

    .about .container .right .details p {
        font-size: 1.3em;
    }
}
.projects {
    height: 90vh;
    background: #122333;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
}

.projects::-webkit-scrollbar {
    background: #182b3c;
    width: 15px;
    position: absolute;
    border-radius: 25px;
}

.projects::-webkit-scrollbar-thumb {
    background: #2eac68;
    border-radius: 25px;
    box-shadow: inset 0 0 5px 2px #185333;
}

.projects>h1 {
    font-family: 'Sacramento', cursive;
    color: aliceblue;
    font-size: 4em;
    font-weight: 100;
}

.projects h1 span {
    color: #2eac68;
    font-weight: 600;
}

.projects .btns {
    width: 50%;
    display: flex;
    justify-content: space-evenly;
    margin: 0 0 5vh 0;
}

.projects .btns button {
    width: 110px;
    font-size: 17px;
    font-family: 'Quicksand';
    font-weight: 600;
    border-radius: 10px;
    outline: none;
    border: none;
    background: #273a4b;
    color: aliceblue;
    box-shadow: 0 0 10px 2px #0e1822;
    height: 6vh;
    transition: .3s ease;
}

.projects .btns button.active {
    box-shadow: inset 0 0 10px 2px #0e1822;
    color: #2eac68;
}

.projects .projects-cont {
    width: 95%;
    display: flex;
    flex-wrap: wrap;
}

.projects .projects-cont .project {
    height: 300px;
    width: 300px;
    border: 21px solid #182b3c;
    border-radius: 11px;
    overflow: hidden;
    word-wrap: break-word;
    position: relative;
    display: flex;
    justify-content: center;
    margin: 2vh 2vh 0 0;
    box-shadow: 0 0 18px 7px #111e2a;
}

.projects .projects-cont .project:hover::after {
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    background: #273a4b;
}

.projects .projects-cont .project::after {
    content: '';
    position: relative;
    height: 100%;
    width: 100%;
    background: transparent;
    transition: .5s ease;
}

.projects .projects-cont .project :is(h1, p) {
    opacity: 0;
}

.projects .projects-cont .project:hover :is(h1, p) {
    opacity: 1;
}

.projects .projects-cont .project :is(img, h1, p) {
    position: absolute;
    transition: .3s ease;
}

.projects .projects-cont .project :is(h1, p) {
    z-index: 2;
    width: 284px;
    text-align: center;

}

.projects .projects-cont .project p {
    bottom: 0;
    font-family: 'Quicksand';
    color: aliceblue;
}

.projects .projects-cont .project h1 {
    font-family: 'Play';
    color: #2eac68;
}

@media only screen and (max-width: 768px) {
    #mouse-circle {
        display: none;
    }

    .projects h1 {
        font-size: 3em;
    }

    .projects .btns {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }

    .projects .btns button {
        margin: 1vh 0;
    }

    .projects .projects-cont {
        display: flex;
        justify-content: center;
    }

    .projects .projects-cont .project {
        position: relative;
    }

    .projects .projects-cont .project h1 {
        position: absolute;
        z-index: 2;
        text-shadow: 0px 0px 5px #020202f3;
        opacity: 1;
    }

    .projects .projects-cont .project p {
        position: absolute;
        z-index: 2;
        text-shadow: 0px 0px 5px #020202f3;
        opacity: 1;
    }

    .projects .projects-cont .project img {
        display: none;
    }
}
.contact {
    height: 90vh;
    background: #122333;
}

.contact .top {
    text-align: center;
    font-size: 2em;
    color: aliceblue;
    position: relative;
    top: 3vh;
}

.contact .top h1 {
    margin: 0;
    font-family: 'Sacramento', cursive;
    font-weight: 100;
}

.contact .top h1 span {
    color: #2eac68;
}

.contact .bottom {
    display: flex;
    align-items: center;
    height: 70vh;
    position: relative;
    top: 5vh;
    justify-content: space-evenly;
}

.contact .bottom .left img {
    height: 60vh;
    filter: hue-rotate(331deg);
}

.contact .bottom .right {
    height: 84%;
    width: 50%;
    display: flex;
    flex-direction: column;
    background: #182b3c;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 10px;
}

.contact .bottom .right form .grid {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-gap: 3vh;
}

.contact .bottom .right form :is(textarea, input[type=text]) {
    width: 18vw;
    height: 7vh;
    background: #21374a;
    border: none;
    outline: none;
    border-radius: 7px;
    box-shadow: inset 0 0 10px 3px #122230;
    color: aliceblue;
    padding: 0 2vh;
    font-size: 1.1em;
    font-family: 'Quicksand';
}

.contact .bottom .right form button {
    width: 10vw;
    height: 7vh;
    border-radius: 25px;
    border: none;
    outline: none;
    background: #29445a;
    box-shadow: 0 0 13px 3px #111d28;
    color: #2eac68;
    font-size: 1.4em;
    font-family: 'Quicksand';
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    transition: .5s ease;
}

.contact .bottom .right form button span {
    color: #89ff00;
    mix-blend-mode: difference;
    pointer-events: none;
}

.contact .bottom .right form button::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 0%;
    background: #2eac68;
    top: 0;
    left: 0;
}

.contact .bottom .right form button.sent::before {
    animation: sendit 5s;
}

@keyframes sendit {
    from {
        width: 0%;
    }

    to {
        width: 100%;
    }
}

.contact .bottom .right form textarea {
    width: 80% !important;
    box-shadow: inset 0 0 15px 3px #13212e !important;
    font-size: 1.5em !important;
    height: 36% !important;
    resize: none;
    padding: 2vh 0 0 2vh !important;
}

.contact .bottom .right form :is(textarea, input[type=text]):-ms-input-placeholder {
    color: #2eac68;
}

.contact .bottom .right form :is(textarea, input[type=text])::placeholder {
    color: #2eac68;
}

.contact .bottom .right form {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

@media only screen and (max-width: 768px) {
    .contact {
        overflow-y: scroll;
    }

    .contact::-webkit-scrollbar {
        background: transparent;
    }

    .contact .top h1 {
        font-size: 1.5em;
    }

    .contact .bottom {
        display: flex;
        flex-direction: column;
    }

    .contact .bottom .left {
        width: 60vw;
        height: 30vh;
    }

    .contact .bottom .left img {
        width: 100%;
        height: 100%;
    }

    .contact .bottom .right {
        width: 95vw;
    }

    .contact .bottom .right form .grid input[type=text] {
        border-radius: 25px;
        border: none;
        outline: none;
        height: 5vh;
        padding: 0 1vh;
        width: 40vw;
        background: #29445a;
    }

    .contact .bottom .right form .grid input[type=text]:-ms-input-placeholder {
        color: #2eac68;
    }

    .contact .bottom .right form .grid input[type=text]::placeholder {
        color: #2eac68;
    }

    .contact .bottom .right form textarea {
        border-radius: 25px;
        border: none;
        outline: none;
        background: #29445a;
    }

    .contact .bottom .right form textarea:-ms-input-placeholder {
        color: #2eac68;
    }

    .contact .bottom .right form textarea::placeholder {
        color: #2eac68;
    }

    .contact .bottom .right form button {
        width: 30vw;
    }
}
